import Page from 'components/Page';
import PropTypes from 'prop-types';
import React from 'react';
import { connect } from 'react-redux';
import { Card, CardBody, CardHeader, Col, Row, Table, Form, Button, FormGroup, Label, Input } from 'reactstrap';

import * as actions from '../store/actions/index';
import DateRange from '../components/Filter/DateRange'
import NotificationSystem from 'react-notification-system';
import { NOTIFICATION_SYSTEM_STYLE } from 'utils/constants';
import RowNoData from '../components/RowNoData';
import BasePage from './BasePage';
import { MdError } from 'react-icons/md';
import formatDate from '../utils/formatDate';
import PaginationFooter  from '../components/Pagination/PaginationFooter';
import PaginationHeader from '../components/Pagination/PaginationHeader';

const date = new Date();

class AccountingCardPage extends BasePage {

	state = {
		filters: {
			open: true,
			from: formatDate(new Date(date.getFullYear(), date.getMonth(), 1)),
			to: formatDate(new Date()),
			year: null,
			type: null,
		},
		paginateOptions:[10,20,30],
		currentPage:null,
		paginateElements:10
	}



	handleSubmit = () => {
		if (this.state.filters.type === null || this.state.filters.type === '') {
			this.notifyMissingField('Seleziona tipo')
			return
		}


		const { searchClicked } = this.state
		this.setState({
			searchClicked: !searchClicked
		})
		this.props.onLoadStats(this.state.currentPage, this.state.paginateElements, this.state.filters, [], null)
	}

	handleChangePagination = (e) => {

		this.setState({ paginateElements: e }, () => {
				this.handleSubmit()
		});

	}

	handleChangePage = (e) => {

		this.setState({ currentPage: e }, () => {
				this.handleSubmit()
		});

	}

	notifyMissingField(message) {
		if (!this.notificationSystem) {
			return;
		}

		this.notificationSystem.addNotification({
			title: <MdError />,
			message: message,
			level: 'error',
		});
	}

	handleFilterChange = (value, name) => {
		const { filters } = this.state
		filters[name] = value
		this.setState({
			filters
		})
	}

	render() {
		let {
			stats
		} = this.props
		let pagination = null
		let totHave = 0
		let totGive = 0
		let totFinal = 0
		let rows = <RowNoData colspan={6} />
		if (this.props.loading === false && this.props.error === false && this.props.stats !== null) {
			rows = stats.map((row, index) => {
				const have = parseFloat(row.attributes.have.replace(',', '.'))
				const give = parseFloat(row.attributes.give.replace(',', '.'))
				totGive += give
				totHave += have
				totFinal += give - have
				return <tr key={index}>
					<td>{row.attributes.card_type}</td>
					<td>{row.attributes.business_name}</td>
					<td>{row.attributes.vat_number}</td>
					<td>{give}</td>
					<td>{have}</td>
					<td>{(give - have).toFixed(2)}</td>
				</tr>
			})

			pagination = <PaginationFooter
			 currentPage={this.props.pagination != null ? this.props.pagination.current_page : null}
				lastPage={this.props.pagination != null ? this.props.pagination.last_page : null}
				handleChangePage={this.handleChangePage.bind(this)}/>
		}
		return (
			<Page title="Sitauzione contabile" breadcrumbs={[{ name: 'situazione contabile', active: true }]}>
				<Row>
					<Col>
						<Card className="mb-3">
							<CardHeader>
								Filtri
                            </CardHeader>
							<CardBody>
								<Form onSubmit={this.handleSubmit}>
									<Row>
										<Col md={4}>
											<FormGroup>
												<Label for="type">Tipo scheda</Label>
												<Input type="select" name="type" id="type" onChange={(event) => this.handleFilterChange(event.target.value, 'type')}>
													<option></option>
													<option value="C">Cliente</option>
													<option value="F">Fornitore</option>
												</Input>
											</FormGroup>
										</Col>
										<Col md={4}>
											<FormGroup>
												<Label for="year">Anno</Label>
												<Input type="number" name="year" id="year" onChange={(event) => this.handleFilterChange(event.target.value, 'year')} />
											</FormGroup>
										</Col>
										<Col md={4}>
											<FormGroup check>
												<Label check>
													<Input type="checkbox" name="open" checked={this.state.filters.open}
														onChange={(event) => this.handleFilterChange(event.target.checked, 'open')} />{' '}
													Solo partite aperte
														</Label>
											</FormGroup>
										</Col>
									</Row>
									<DateRange onChange={this.handleFilterChange.bind(this)} fromStartDate={this.state.filters.from} toStartDate={this.state.filters.to} />
									<Row>
										<Col>
											<Button
												color="primary"
												disabled={this.props.loading && this.state.searchClicked}
												onClick={this.handleSubmit}>
												Cerca
                                            </Button>
										</Col>
									</Row>
								</Form>
							</CardBody>
						</Card>
						<CardHeader>
								<Row>
										<Col  sm="12" md={{ size: 3, offset: 9}}>
											<PaginationHeader options={this.state.paginateOptions} handleChangePagination={this.handleChangePagination}/>
										</Col>
								</Row>
						</CardHeader>
						<Card className="mb-3">
							<CardBody>
								<Table responsive>
									<thead>
										<tr>
											<th>Tipo</th>
											<th>Ragione sociale</th>
											<th>Partita iva</th>
											<th>Dare €</th>
											<th>Avere €</th>
											<th>Saldo finale €</th>
										</tr>
									</thead>
									<tbody>
										{rows}
									</tbody>
									<tfoot>
										<tr>
											<th colSpan="3">TOTALI:</th>
											<td>€ {totGive.toFixed(2)}</td>
											<td>€ {totHave.toFixed(2)}</td>
											<td>€ {totFinal.toFixed(2)}</td>
										</tr>
									</tfoot>
								</Table>
							</CardBody>
						</Card>
					</Col>
				</Row>
				{pagination}
				<NotificationSystem
					dismissible={false}
					ref={notificationSystem =>
						(this.notificationSystem = notificationSystem)
					}
					style={NOTIFICATION_SYSTEM_STYLE}
				/>
			</Page>
		);
	}
}
AccountingCardPage.contextTypes = {
	store: PropTypes.object
};

AccountingCardPage.propTypes = {
	notifications: PropTypes.array,
	stats: PropTypes.array,
	loading: PropTypes.bool,
	error: PropTypes.bool
};

const mapStateToProps = state => {
	return {
		stats: state.statistics.accountingCardStatistics,
		loading: state.statistics.loading,
		error: state.statistics.error,
		notifications: state.notifications,
		pagination: state.statistics.pagination,
	};
};

const mapDispatchToProps = dispatch => {
	return {
		onLoadStats: (page, perPage, filters, include, order) => dispatch(actions.loadAccountingCardStat(page, perPage, filters, include, order)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(AccountingCardPage);
